@import url('https://fonts.googleapis.com/css2?family=Akaya+Telivigala&display=swap');
.App {
  grid-template-columns: 40px, auto, 40px;
  grid-template-rows: 40px, auto;
}
.text {
  font-family: 'Akaya Telivigala', cursive;
  padding: 5px;
  text-align: center;
  border-bottom: 1px solid;
  border-color: #b8b8b8;
  padding-left: 25%;
  padding-right: 25%;
  align-self: center;
}
#featured {

  align-self: center;
  border: 1px solid;
  border-color: #b8b8b8;
  width: 65%;
  text-align: center;
}
#logo {
  grid-column: 1;
  grid-row: 1;
}
#cookies {
 grid-column: 2;
  grid-row: 1;
  grid-template-columns: 10px, auto, 10px;
  grid-template-rows: auto, 10px;
  background-color: #1ed2ff;
  border-bottom: 1px solid;
  border-color: #b8b8b8;
  font-family: 'Akaya Telivigala', cursive;
  padding: 5px;
  text-align: center;
}
#cookieText {
  grid-column: 2;
  grid-row: 1;
}
#cookieButton {
  grid-column: 3;
  grid-row: 2;
  font-family: 'Akaya Telivigala', cursive;
  font-size: 25px;
  background-color: #00d9ff;
  border: 0px solid;
  size: 10px
}